					name: 'olive',
					color: '#8dc63f'
				},
				{
					title: '森绿',
					name: 'green',
					color: '#39b54a'
				},
				{
					title: '天青',
					name: 'cyan',
					color: '#1cbbb4'
				},
				{
					title: '海蓝',
					name: 'blue',
					color: '#0081ff'
				},
				{
					title: '姹紫',
					name: 'purple',
					color: '#6739b6'
				},
				{
					title: '木槿',
					name: 'mauve',
					color: '#9c26b0'
				},
				{
					title: '桃粉',
					name: 'pink',
					color: '#e03997'
				},
			],
			uid: 0,
			title: '',
			date: '2020-01-01',
			note: '',
			img: 0,
			modalName: null,
			noticeId:0,
			notice: [
				"最是人间留不住，朱颜辞镜花辞树。",
				"轻描淡写的曾经，却是难忘的刻骨铭心。",
				"天空没有痕迹，风雨已在心中。",
				"谁说“日月既往，不可复追”",
				"从前的锁也好看，钥匙精美有样子",
			]
		};
	},
	methods: {
		showModal: function(e) {
			this.modalName = 'Modal';
		},
		hideModal: function(e) {
			this.modalName = null
		},
		DateChange: function(e) {
			this.date = e.detail.value
		},
		titleInput: function(e) {
			this.title = e.detail.value
		},
		noteInput: function(e) {
			this.note = e.detail.value
		},
		selectImg: function(index) {
			this.img = index;
		},
		create: function() {
			const _this = this;
			if (!this.checkEmpty()) {
				console.log("没有名字是不会被记住的哦~");
			} else {
				uni.request({
					method: 'GET',
					data: {
						uid: _this.uid,
						date: _this.date,
						title: _this.title,
						notes: _this.note,
						img: _this.img
					},
					url: 'http://localhost:8080/api/bigDay/build',
					success: (res) => {
						if (res.data.err == 0) {
							_this.noticeId = Math.floor(Math.random() * 10) % 5;
							console.log(_this.noticeId);
							_this.showModal();
							setTimeout(function() {
								_this.hideModal();
								uni.switchTab({
									url: './bigDay'
								})
							}, 1500);
						} else {
							console.log("Create failed.");
						}
					}
				})
			}

		},
		checkEmpty: function() {
			if (this.title === '') {
				return false;
			} else {
				return true;
			}
		},

	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	        console.log(option.uid); //打印出上个页面传递的参数。
	        console.log(option.id); //打印出上个页面传递的参数。
	    }
